'use strict';
import axios from 'axios';
import Vue from 'vue';

const CHAT_STATE = {
  IDLE: 0,
  THINK: 1,
  REPLY: 2
}

const EMOTION = {
  ANGER: 'anger',
  JOY: 'joy',
  CURIOUS: 'curious',
  LOVE: 'love',
  CAUTIOUS: 'cautious',
  NONE: 'none'
}

declare global {
  interface Window {
    SpeechRecognition: any;
    webkitSpeechRecognition: any;
  }
}

//let FB = window.FB; // ok now

const outputYou = document.querySelector('.output-you');
const outputBot = document.querySelector('.output-bot');

const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
const recognition = new SpeechRecognition();

recognition.lang = 'en-US';
recognition.interimResults = false;
recognition.maxAlternatives = 1;

let vueApp: any = null;

let lastEmotion = "";
let lastOutput = "";

//const socket = io();

// document.querySelector('button')?.addEventListener('click', () => {
//   recognition.start();
// });

recognition.addEventListener('speechstart', () => {
  console.log('Speech has been detected.');
});

recognition.addEventListener('result', (e: any) => {
  console.log('Result has been detected.');

  let last = e.results.length - 1;
  let text = e.results[last][0].transcript;
  console.log(text);

  vueApp.chatState = CHAT_STATE.THINK;
  axios.get('http://130.216.217.186:8080/poem?input='+text).then(response=>{
    console.log(response.data.output);
    vueApp.chatState = CHAT_STATE.REPLY;
    lastEmotion = response.data.emotion;
    lastOutput = response.data.output;
    synthVoice(lastOutput);
    vueApp.setBackgroundBasedOnEmotion(lastEmotion);
  }).finally(()=>{
    
  });

  if (outputYou) {
    outputYou.textContent = text;

  }
  console.log('Confidence: ' + e.results[0][0].confidence);

  //socket.emit('chat message', text);
});

recognition.addEventListener('speechend', () => {
  recognition.stop();
});

recognition.addEventListener('error', (e: any) => {
  if (outputBot) {
    outputBot.textContent = 'Error: ' + e.error;
  }
});

function synthVoice(text: any) {
  vueApp.chatState = CHAT_STATE.REPLY;
  const synth = window.speechSynthesis;
  const utterance = new SpeechSynthesisUtterance();
  utterance.text = text;
  synth.speak(utterance);
}

export default Vue.extend(
  {
    name: 'Home',
    components: {
      
    },
    destroyed() {
      
    },
    mounted() {
      
    

    },
    created(){
      this._log("created");
      vueApp = this;
    },
    data() {
      return {
        isChatting: false,
        CHAT_STATE: CHAT_STATE,
        chatState: CHAT_STATE.IDLE,
        chatbackground: EMOTION.NONE
      }

    },
    methods: {
      _log(...msg:any): void {
        console.log("|Home|", ...msg);
      },
      _err(...msg:any): void {
        console.error("|Home|", ...msg);
      },
      startReco(){
        recognition.start();
      },
      setBackgroundBasedOnEmotion(emotion: string){
        console.log(emotion);
        this.chatbackground = emotion;
      }
      
    },
    watch: {
      
    },
    computed: {
      
    }
  }
);


